<template>
  <div>
    <v-row class="title_row">
      <v-col class="title_col">
        <div
          v-if="theme === 'green'"
          class="title-green"
        >
          <h1 :class="(uppercase) ? `text-uppercase` : ''">
            {{ setCompleteTitle }}
          </h1>
        </div>
        <div
          v-else
          class="title-orange"
        >
          <h1 :class="(uppercase) ? `text-uppercase` : ''">
            {{ setCompleteTitle }}
          </h1>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
  export default {
    name: 'TitleRibbon',
    props: {
      title: {
        type: String,
        default: '',
      },
      sucursal: {
        type: Number,
        default: null,
      },
      theme: {
        type: String,
        default: 'green' /* green - orange */,
      },
      uppercase: {
        type: Boolean,
        default: true,
      },
    },
    computed: {
      setCompleteTitle () {
        let str = ''
        switch (this.sucursal) {
          case 1:
            str = `${this.title} Providencia`
            break
          case 2:
            str = `${this.title} Vi√±a del Mar`
            break
          case 3:
            str = `${this.title} Santiago Centro`
            break
          default:
            str = this.title
            break
        }

        return str
      },
    },
  }
</script>

<style lang="sass" scoped>
@import '@/sass/variables2'

@mixin title($color, $background)
  display: flex
  justify-content: center
  width: 100%
  padding: 16px
  background: $background !important
  color: $color !important

.title_row
  .title_col
    // padding: 10px 24px 10px 24px
    padding-top: 10px
    padding-botom: 10px
    .title-green
      @include title($darkgreen, $lightgreen)
    .title-orange
      @include title($darkorange, $lightorange)

    h1
      font-size: 27px
      @media (max-width: 600px)
        font-size: 5vw
        text-align: center
</style>
